body {
  background-color: #2b2b2b;
  color: #f5f5f5;
  font-family: 'Arial', sans-serif;
  padding: 20px;
}

textarea {
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  padding: 20px;
  font-size: 16px;
  background-color: #3c3c3c;
  color: #f5f5f5;
  border: none;
  border-radius: 4px;
}

button {
  margin: 10px 10px;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #61dafb;
  border: none;
  border-radius: 4px;
  color: #2b2b2b;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

button:hover {
  background-color: #4cafda;
  color: #f5f5f5;
}

pre {
  padding: 20px;
  background-color: #3c3c3c;
  border-radius: 4px;
  white-space: pre-wrap;  
  word-wrap: break-word;  
}
